(function () {

  angular.module('meanApp', ['ngRoute', 'ngFileUpload']);

  function config ($routeProvider, $locationProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'home/home.view.html',
        controller: 'homeCtrl',
        controllerAs: 'vm'
      })
      .when('/register', {
        templateUrl: '/auth/register/register.view.html',
        controller: 'registerCtrl',
        controllerAs: 'vm'
      })
      .when('/login', {
        templateUrl: '/auth/login/login.view.html',
        controller: 'loginCtrl',
        controllerAs: 'vm'
      })
      .when('/logout', {
        templateUrl: '/auth/logout/logout.view.html',
        controller: 'logoutCtrl',
        controllerAs: 'vm'
      })
      .when('/profile', {
        templateUrl: '/profile/profile.view.html',
        controller: 'profileCtrl',
        controllerAs: 'vm'
      })
      .when('/teacher', {
        templateUrl: '/teacher/teacher.view.html',
        controller: 'teacherCtrl',
        controllerAs: 'vm'
      })
      .when('/video', {
        templateUrl: '/videos/videos.view.html',
        controller: 'videosCtrl',
        controllerAs: 'vm'
      })
      .when('/student', {
        templateUrl: '/student/student.view.html',
        controller: 'studentCtrl',
        controllerAs: 'vm'
      })
      .when('/subjects', {
        templateUrl: '/subjects/subjects.view.html',
        controller: 'subjectsCtrl',
        controllerAs: 'vm'
      })
      .otherwise({redirectTo: '/'});

    // use the HTML5 History API
    $locationProvider.html5Mode(true);
  }

  function run($rootScope, $location, authentication) {
    $rootScope.$on('$routeChangeStart', function(event, nextRoute, currentRoute) {
      if ($location.path() === '/profile' && !authentication.isLoggedIn()) {
        $location.path('/');
      }
    });
  }
  
  angular
    .module('meanApp')
    .config(['$routeProvider', '$locationProvider', config])
    .run(['$rootScope', '$location', 'authentication', run]);

})();
